:root {
    --primary: #54a8fc;
    --light: #d9d9d9;
    --dark: #121212;
    --gray: #414344;
    --second: rgba(12, 12, 12, 0.6);
    --checked: false;
    --toggle-width: 25px;
    /* Smaller toggle width */
}

@media (max-width: 768px) {
    :root {
        --toggle-width: 18px;
        /* Smaller width for mobile devices */
    }
}

@media (min-width: 1024px) {
    :root {
        --toggle-width: 32px;
        /* Larger width for bigger screens */
    }
}

.toggle-cont {
    position: relative;
    z-index: 10;
    width: fit-content;
    height: 25px;
    /* Smaller height */
    border-radius: 9999px;

    /* Positioning */
    position: fixed;
    top: 1%;
    right: 1%;

    @media (max-width: 768px) {
        right: 1%;
        top: 1%;
    }

    @media (min-width: 1024px) {
        right: 1%;
    }
}

.toggle-cont .toggle-input {
    display: none;
}

.toggle-cont .toggle-label {
    --gap: 2px;
    /* Reduced gap for a tighter layout */
    --width: var(--toggle-width);

    cursor: pointer;
    position: relative;
    display: inline-block;

    padding: 0.2rem;
    /* Reduced padding for a smaller button */
    width: calc((var(--width) + var(--gap)) * 2);
    height: 100%;
    background-color: var(--dark);

    border: 0.3px solid #777777;
    /* Thinner border */
    border-bottom: 0;
    border-radius: 9999px;
    box-sizing: content-box;
    transition: all 0.3s ease-in-out;
}

.toggle-label::before {
    content: "";
    position: absolute;
    z-index: -10;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% + 0.8rem);
    /* Adjusted to fit the smaller toggle */
    height: calc(100% + 0.8rem);
    background-color: var(--gray);

    border: 0.3px solid #777777;
    /* Thinner border */
    border-bottom: 0;
    border-radius: 9999px;
    transition: all 0.3s ease-in-out;
}

.toggle-label::after {
    content: "";
    position: absolute;
    z-index: -10;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    background-image: radial-gradient(circle at 50% -100%,
            rgb(58, 155, 252) 0%,
            rgba(12, 12, 12, 1) 80%);
    border-radius: 9999px;
}

.toggle-cont .toggle-label .cont-icon {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    width: var(--width);
    height: 25px;
    /* Adjusted height to match the smaller size */
    background-image: radial-gradient(circle at 50% 0%,
            #666666 0%,
            var(--gray) 100%);

    border: 0.3px solid #aaaaaa;
    /* Thinner border */
    border-bottom: 0;
    border-radius: 9999px;
    box-shadow: inset 0 -0.1rem 0.1rem var(--primary),
        inset 0 0 0.2rem 0.4rem var(--second);

    transition: transform 0.3s ease-in-out;
}

.cont-icon {
    overflow: clip;
    position: relative;
}

.cont-icon .sparkle {
    position: absolute;
    top: 50%;
    left: 50%;

    display: block;
    width: calc(var(--width) * 0.5px);
    /* Smaller sparkles */
    aspect-ratio: 1;
    background-color: var(--light);
    border-radius: 50%;
    transform-origin: 50% 50%;
    rotate: calc(1deg * var(--deg));
    transform: translate(-50%, -50%);
    animation: sparkle calc(100s / var(--duration)) linear calc(0s / var(--duration)) infinite;
}

@keyframes sparkle {
    to {
        width: calc(var(--width) * 0.3px);
        transform: translate(2000%, -50%);
    }
}

.toggle-input:checked+.toggle-label .cont-icon {
    overflow: visible;
    background-image: radial-gradient(circle at 50% 0%,
            #045ab1 0%,
            var(--primary) 100%);

    border: 0.3px solid var(--primary);
    /* Thinner checked border */
    border-bottom: 0;

    /* Slide effect instead of rotate */
    transform: translateX(calc((var(--width) + var(--gap))));
    /* Horizontal slide */
}

.toggle-cont .toggle-label::before {
    box-shadow: 0 0.5rem 1.5rem -1rem #0080ff;
    /* Adjusted shadow for smaller size */
}

.toggle-input:checked+.toggle-label .cont-icon .sparkle {
    z-index: -10;
    width: calc(var(--width) * 1.2px);
    /* Adjusted for smaller toggle */
    background-color: #acacac;
    animation: sparkle calc(100s / var(--duration)) linear calc(10s / var(--duration)) infinite;
}

@keyframes sparkle {
    to {
        width: calc(var(--width) * 0.8px);
        transform: translate(5000%, -50%);
    }
}